import router from './router'
import store from './store'
import { getCookie } from './utils/cookie'      // cookie

// 
// function hasPermission(roles, permissionRoles) {
//   console.log(roles,permissionRoles)
//   if (!permissionRoles) return true
//   else{
//     return roles.some(role => permissionRoles.indexOf(role) >= 0)
//   }
// }

const whiteList = ['/','/forgetPassword']                  // 

router.beforeEach((to, from, next) => {
  if (getCookie('byd_performance')) {  
    if (to.path === '/') {                                 // 
      next({ path: '/personal' })                                       // 
    } else {                                                 
      if (store.getters.addRouters.length === 0) {   // user_info
        store.dispatch('GetUserInfo')  //
          .then(res=>{
            // console.log(res)
            //
            // const roles=res.data.roles;
            //API
            const roles=res.data.detail.role;
            store.dispatch('GenerateRoutes', roles ).then(() => { // 
              router.addRoutes(store.getters.addRouters) // 
              next({ ...to, replace: true }) // hack addRoutes ,set the replace: true so the navigation will not leave a history record
            })
          }).catch(err => {                                              // 
              console.error(err)
          })
      } else {
          // bug
          // console.log(store.getters.roles,to.path.role)
          // if (hasPermission(store.getters.roles, to.role)) {
          //   next()
          // } else {
          //   next( '/403')
          // }
          //
          next()
      }
    }
  } else {                                    
    whiteList.includes(to.path)                     // 
      ? next()
      : next('/')
  }
})




