// 
const baseSize = 16
//  rem 
function setRem () {
  // 1920
  const scale = document.documentElement.clientWidth / 1280
  // 
  document.documentElement.style.fontSize = (baseSize * Math.min(scale, 2)) + 'px'
}
// 
setRem()
//  rem
window.onresize = function () {
  setRem()
}



